.canvas:focus{
    outline: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: crosshair;
}

.core-button{
    background-color: rgb(255, 133, 52);
    color: white;
    
    border-radius: 4px;
    font-size: x-large;
    font-weight: 800;
    letter-spacing: 1.5px;
}

.core-button:hover{
    background-color: white;
    color: rgb(255, 133, 52);
}